.messages {
	&__inner {
		padding: 40px 20px;
		margin: 0 auto;
		max-width: 680px;
		@include xs {
			padding: 30px 20px; } }
	&__header {
		margin-bottom: 30px;
		@include clr; }
	&__select {
		float: left;
		max-width: 280px;
		@include r(500) {
			max-width: 130px; } }
	&__btn {
		float: right; }
	&__pagination {
		margin-top: 30px; } }
