.grid {
	display: table;
	table-layout: fixed;
	border: 1px solid #ccc;
	width: 100%;
	&__header {
		display: table-header-group;
		background: #e6e6e6;
		.grid__column {
			vertical-align: middle;
			&_checkbox {
				font-size: 0; } }
		.grid__header-row {
			&:not(:first-child) {
				.grid__column {
					border-top: 1px solid #ccc; } } } }
	&__body {
		display: table-row-group;
		.grid__column {
			vertical-align: top;
			border-top: 1px solid #ccc;
			&_checkbox,
			&_enabled,
			&_edit {
				font-size: 0; } } }
	&__header-row,
	&__row {
		display: table-row; }
	&__row {
		cursor: pointer;
		font-size: 16px;
		@include hover {
			.grid__btn {
				opacity: 1; } }
		&:nth-child(odd) {
			background: $lightGray; } }
	&__column {
		display: table-cell;
		padding: 10px 5px;
		&_enabled,
		&_edit {
			text-align: center; }
		&_enabled {
			width: 75px; }
		&_checkbox {
			padding-left: 10px;
			width: 45px; }
		&_edit {
			padding-right: 10px;
			width: 45px; }
		&_message {
			width: 35%;
			@include r(660) {
				width: 80%; } }
		&_to {
			width: 24%; }
		&_channel,
		&_to {
			@include r(660) {
				display: none; } } }
	&__btn {
		opacity: 0;
		@include touch {
			opacity: 1; } }
	&__icon {
		fill: #ccc;
		font-size: 22px;
		&_active {
			fill: $green; } } }
