.checkbox {
  position: relative;
  display: inline-block;
  vertical-align: top;
  min-width: 30px;
  min-height: 30px;
  user-select: none;
}

.checkbox_middle .checkbox__text {
  padding-top: 5px;
  padding-bottom: 5px;
}

.checkbox input {
  display: none;
}

.checkbox input:checked ~ .checkbox__icon .icon {
  opacity: 1;
}

.checkbox input:disabled ~ .checkbox__icon, .checkbox input:disabled ~ .checkbox__text {
  opacity: 0.5;
  cursor: not-allowed;
}

.checkbox_sm {
  min-width: 24px;
  min-height: 24px;
}

.checkbox_sm .checkbox__icon {
  width: 24px;
  height: 24px;
}

.checkbox_sm .checkbox__icon .icon {
  font-size: 10px;
}

.checkbox__icon {
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid #ccc;
  border-radius: 3px;
  width: 30px;
  height: 30px;
  background: #fff;
  cursor: pointer;
  transition: border-color 0.25s;
}

.checkbox__icon .icon {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  opacity: 0;
  fill: #333;
  font-size: 14px;
  transform: translate3d(-50%, -50%, 0);
  transition: opacity 0.25s, background 0.25s;
}

.checkbox__text {
  display: inline-block;
  vertical-align: top;
  padding: 5px 0 5px 40px;
  cursor: pointer;
  transition: color 0.25s;
}

.awesome-switcher {
  position: relative;
  display: inline-block;
  vertical-align: top;
  margin: 0;
  height: 30px;
  font-size: 0;
  cursor: pointer;
  user-select: none;
  appearance: none;
}

.awesome-switcher:active .awesome-switcher__control:before {
  box-shadow: inset 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.awesome-switcher__input {
  display: none;
}

.awesome-switcher__input:checked + .awesome-switcher__control {
  border-color: #2e99db;
}

.is-metro .awesome-switcher__input:checked + .awesome-switcher__control {
  border-color: #141547;
}

.is-boost .awesome-switcher__input:checked + .awesome-switcher__control {
  border-color: #EF902D;
}

.awesome-switcher__input:checked + .awesome-switcher__control:before {
  background: #2e99db;
}

.is-metro .awesome-switcher__input:checked + .awesome-switcher__control:before {
  background: #141547;
}

.is-boost .awesome-switcher__input:checked + .awesome-switcher__control:before {
  background: #EF902D;
}

.awesome-switcher__input:checked + .awesome-switcher__control:after {
  box-shadow: -1px 2px 2px rgba(0, 0, 0, 0.2);
  transform: translate3d(10px, 0, 0);
}

.awesome-switcher__control {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 50px;
  height: 100%;
  border-radius: 15px;
  border: 1px solid #ccc;
  transition: border-color 0.2s ease-in-out;
}

.awesome-switcher__control:before, .awesome-switcher__control:after {
  content: '';
  position: absolute;
}

.awesome-switcher__control:before {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 15px;
  background: #eee;
  transition: background 0.2s ease-in-out, box-shadow 0.1s ease-in-out;
}

.awesome-switcher__control:after {
  top: 50%;
  left: 50%;
  margin: -12px 0 0 -12px;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  background: #fff;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
  transform: translate3d(-10px, 0, 0);
  transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
}

