.modal {
  position: fixed;
  z-index: 200;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
  padding: 20px;
  opacity: 0;
  visibility: hidden;
  background: rgba(51, 51, 51, 0.8);
  text-align: center;
  font-size: 0;
  transition: opacity 0.25s, visibility 0.25s;
}

.modal:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.modal.is-open {
  opacity: 1;
  visibility: visible;
}

.modal_sm .modal__container {
  max-width: 400px;
}

.modal__container {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  max-width: 600px;
}

.modal__close {
  position: absolute;
  z-index: 2;
  top: 5px;
  right: 5px;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  color: #fff;
  background: none;
  text-align: center;
  font-size: 0;
  transition: background 0.25s, box-shadow 0.25s;
}

.modal__close:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.no-touch .modal__close:hover {
  background: rgba(0, 0, 0, 0.05);
}

.modal__close:active {
  background: rgba(0, 0, 0, 0.1) !important;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2) !important;
}

.modal__close .icon {
  display: inline-block;
  vertical-align: middle;
  fill: currentColor;
  font-size: 18px;
}

.modal-content {
  color: #333;
  background: #fff;
  box-shadow: 0 3px 7px 1px rgba(0, 0, 0, 0.1);
}

.modal-content__header {
  padding: 5px 60px 5px 20px;
  height: 60px;
  color: #fff;
  background: #2e99db;
  text-align: center;
  font-size: 0;
  text-align: left;
}

.modal-content__header:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.is-metro .modal-content__header {
  background: #141547;
}

.is-boost .modal-content__header {
  background: #2e2e2e;
}

.modal-content__title {
  display: inline-block;
  vertical-align: middle;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.2;
  text-transform: uppercase;
}

.modal-content__body, .modal-content__footer {
  padding: 20px;
}

.modal-content__body {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.3;
  text-align: left;
  white-space: pre-line;
}

.modal-content__footer {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  text-align: right;
}

@media only screen and (max-width: 480px) {
  .modal-content__btn {
    width: 100%;
  }
}

.modal-content__btn:not(:last-child) {
  margin-right: 20px;
}

@media only screen and (max-width: 480px) {
  .modal-content__btn:not(:last-child) {
    margin: 0 0 10px;
  }
}

.modal-content .modal-content__info {
  padding: 0 0 10px;
}

.loader {
  position: fixed;
  z-index: 200;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  visibility: hidden;
  background: rgba(255, 255, 255, 0.8);
  text-align: center;
  font-size: 0;
  transition: opacity 0.25s, visibility 0.25s;
}

.loader:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.loader.is-open {
  opacity: 1;
  visibility: visible;
}

.loader__center {
  display: inline-block;
  vertical-align: middle;
}

.loader__title {
  color: #333;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.2;
}

.loader__icon {
  position: relative;
  display: block;
  margin: 0 auto;
  width: 1em;
  height: 1em;
  font-size: 150px;
}

.loader__icon:before, .loader__icon:after {
  content: '';
  position: absolute;
  top: 13%;
  left: 13%;
  right: 13%;
  bottom: 13%;
  border-radius: 50%;
  width: 74%;
  height: 74%;
  background: rgba(0, 0, 0, 0.3);
  transform: scale(0);
  animation: scale 2s cubic-bezier(0.1, 0.2, 0.3, 1) infinite;
}

.loader__icon:after {
  animation-delay: 1s;
}

@keyframes scale {
  0% {
    opacity: 1;
    transform: scale(0);
  }
  90% {
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(1);
  }
}

.messages__inner {
  padding: 40px 20px;
