.btn {
	position: relative;
	overflow: hidden;
	display: inline-block;
	vertical-align: top;
	padding: 0 15px;
	min-width: 120px;
	border: none;
	outline: none;
	color: $white;
	background: $blue;
	font-family: inherit;
	@include f(18px, 300, 50px);
	text-align: center;
	text-transform: uppercase;
	text-decoration: none;
	cursor: pointer;
	transition: opacity .175s, background .175s, box-shadow .175s;
	@include metro {
		background: $metroSecondary; }
	@include boost {
		background: $elevatePrimary; }
	&:active {
		color: $white;
		background: darken($blue, 10%);
		box-shadow: inset 2px 2px 6px rgba(0,0,0,.2);
		@include metro {
			background: darken($metroSecondary, 10%); }
		@include boost {
			background: darken($elevatePrimary, 10%); } }
	&:disabled,
	&.is-disabled {
		opacity: 0.7;
		cursor: not-allowed; }
	&_gray {
		background: #bec1cb;
		@include metro {
			background: #bec1cb; }
		@include boost {
			background: #bec1cb; }
		&:active {
			background: darken(#bec1cb, 10%);
			@include boost {
				background: darken(#bec1cb, 10%); }
			@include metro {
				background: darken(#bec1cb, 10%); } } }
	&_red {
		background: $red;
		@include metro {
			background: $red; }
		&:active {
			background: darken($red, 10%);
			@include metro {
				background: darken($red, 10%); } } }
	&_wide {
		width: 100%; }
	&_plus {
		width: 40px;
		min-width: auto;
		height: 40px;
		&:before,
		&:after {
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			width: 18px;
			height: 1px;
			background: currentColor;
			transform: translate(-50%,-50%); }
		&:after {
			transform: translate(-50%,-50%) rotate(90deg); } }
	&_sm {
		font-size: 16px;
		line-height: 40px; }
	&_icon {
		padding: 0;
		width: 30px;
		height: 30px;
		min-width: auto;
		line-height: normal;
		@include vertical; }
	&__edit-icon,
	&__filter-icon,
	&__icon {
		vertical-align: middle;
		fill: currentColor; }
	&__edit-icon,
	&__filter-icon {
		font-size: 16px; }
	&__icon {
		font-size: 10px;
		transform: translateY(1px); } }

.btn-plus {
	position: relative;
	width: 40px;
	height: 40px;
	background: none;
	color: $darkGray;
	&:before,
	&:after {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;
		width: 18px;
		height: 1px;
		background: currentColor;
		transform: translate(-50%,-50%); }
	&:after {
		transform: translate(-50%,-50%) rotate(90deg); } }
